import React, { useEffect } from "react"
import Policy from "../components/Contract/Policy/Content.jsx"
import { AnimatedCursor } from "../components/utils/AnimatedCursor"
import AOS from "aos"
import Layout from "../components/Layout"
import Hero from '../components/NotFound/Hero.jsx'

import "aos/dist/aos.css"
export default function PolicyPage() {
 
  useEffect(() => {
    AOS.init()
  }, [])

  const mouseCoords =
    typeof window !== "undefined" &&
    JSON.parse(window.localStorage.getItem("mouseCoords"))

  return (
 <>
      <Hero />
      <AnimatedCursor
        coord={mouseCoords}
        innerSize={16}
        outerSize={16}
        color="255, 255, 255"
        outerAlpha={0.2}
        innerScale={0.8}
        outerScale={4}
        clickables={[
          "a",
          'input[type="text"]',
          'input[type="email"]',
          'input[type="number"]',
          'input[type="submit"]',
          'input[type="image"]',
          "label[for]",
          "select",
          "textarea",
          "button",
          ".link",
          ".swiper-pagination-bullet",
        ]}
      />
    </>
  )
}
